.titulo{
    margin-bottom:1em;
}
img{
    
    width: 90%;
    border-radius: 2%;
}
.imagen{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2em;
    margin-top: 2em;
}

.costo,.color, .cantidad, .titulo, .cantidad{
    margin-left: .5em;
    margin-top: .2em;
}
.cantidad,.costo, .color{
    font-size: 24px;
    font-weight: bold;
    display: block;
}
.cajabto{
    
    display: grid;
    place-items: center;
}

button{
    margin-top: 1em;
    width: 90%;
    font-size: 1.5em;
}
.enlace{
    width: 90%;
    display: grid;
    place-items: center;
    margin-bottom: 1em;
}

.cantidad{
   width: 30%;
}
